

























import Vue from 'vue'
import Component from 'vue-class-component'
import { State, Action, Getter } from 'vuex-class'
import { constants } from 'fs'
import { fail } from 'assert'
import { setInterval } from 'timers'
import home from '../../request/home'
import PublicAddress from '@/components/publicAddress/PublicAddress.vue'
import Popup from '@/components/popup/Popup.vue'
import Toast from '@/components/toast/Toast.vue'
import mark from '../../utils/mark'
@Component({
  name: 'Default',
  components: {
    PublicAddress,
    Popup,
    Toast
  }
})
export default class Default extends Vue {
  @Action getTypelist: () => void
  @Action setAllList: (params?: object) => void
  @Action setNoviceGuideState: (params?: any) => void
  @State typelist: any
  @State noviceGuideState: number
  allList = []
  listParams = {
    category: 1,
    page: 0
  }
  isEnd = false
  loading = false
  finished = false
  mounted() {
    this.getTypelist()
    this.onLoad()
    this.allList = []
    this.listParams = {
      category: 1,
      page: 0
    }
    this.isEnd = false
    this.loading = false
    this.finished = false
    if (localStorage.getItem('noviceGuideState')) return
    localStorage.setItem('noviceGuideState', 'true')
    this.setNoviceGuideState(1)
  }

  selectNav(index: number) {
    mark({
      module: '打点',
      function: '分类',
      action: '点击分类',
      memo: index
    })
    this.listParams = {
      category: index + 1,
      page: 0
    }
    this.allList = []
    this.onLoad()
  }
  async onLoad() {
    this.listParams.page++
    let res: Ajax.AxiosResponse | any = await home.getAllList(this.listParams)
    if (res && res.code === 200) {
      this.allList = this.allList.concat(res.msg.content)
      console.log(this.allList)
      this.loading = false
      this.isEnd = res.msg.isEnd
      if (this.isEnd) {
        this.finished = true
      } else {
        this.finished = false
      }
    }
  }
}



